<template>
  <v-app id="inspire">
    <nprogress-container></nprogress-container>
    <v-app-bar
      app
      clipped-right
      color="#FFFFFF"
      flat
      elevation="1"

    >
    <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>


      <v-spacer></v-spacer>
        <lang/>
        <div class="pr-2">
            <v-icon color="black" @click="$router.go(-1)">{{icon_home}}</v-icon>
        </div>
    </v-app-bar>

    <v-navigation-drawer
      v-model="drawer"
      app
      color="white"
    >

        <v-sheet
            class="pa-4"
            color="white"
        >
            <router-link to="/">
                <v-img
                    :src="$baseUrl+'storage/icons/110x92px.png'"
                    :lazy-src="$baseUrl+'storage/icons/110x92px.png'"
                    aspect-ratio="2"
                    max-height="95"
                    max-width="250"
                    contain
                >
                    <template v-slot:placeholder>
                    <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                    >
                        <v-progress-circular
                        indeterminate
                        color="grey lighten-5"
                        ></v-progress-circular>
                    </v-row>
                    </template>
                </v-img>
            </router-link>
            <v-divider class="mx-4"></v-divider>
            <v-row  dense justify="center" align="center">
                <v-btn
                v-for="icon in icons"
                :key="icon"
                class="mx-1"
                icon
                >
                <v-icon size="24px">
                    {{ icon }}
                </v-icon>
                </v-btn>
            </v-row>
        </v-sheet>
    </v-navigation-drawer>

    <v-main>
      <v-container
        class="py-8 px-6 mb-8"
        fluid
      >
      <v-row>
          <v-col
            cols="12"
          >
           <slot/>
           <messenges/>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
     <v-btn
            v-scroll="onScroll"
            v-show="fab"
            fab
            dark
            fixed
            bottom
            right
            color="primary"
            @click="toTop"
            class="mb-3"
            small
          >
            <v-icon>mdi-chevron-up</v-icon>
        </v-btn>

    <v-footer padless fixed app color="black">

      <v-col
        class="text-center"
        cols="12"
      >
       <div
            :class="[`text-caption`]"
            class="transition-swing white--text ml-4"
        >
         &copy;{{ new Date().getFullYear() }} — EatInMore
        </div>
      </v-col>
    </v-footer>
  </v-app>

</template>

<script>
  import NprogressContainer from 'vue-nprogress/src/NprogressContainer'
  import lang from "../../Helpers/i18n.vue";
  import messenges from "../../Helpers/Messenges.vue";
  export default {


    components: {
        NprogressContainer,
        lang,
        messenges
    },

    data() {

        return {
            userInfo: localStorage.getItem('user'),
            icon_home: "mdi-account",
            drawer:false,
            icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
            ],
            fab: false,
        }

    },

    computed: {
    },  
    created() {
      
    },
    methods: {
        onScroll (e) {
        if (typeof window === 'undefined') return
        const top = window.pageYOffset ||   e.target.scrollTop || 0
        this.fab = top > 20
        },
        toTop () {
        this.$vuetify.goTo(0)
        },
    },
    mounted() {
        const userData = JSON.parse(this.userInfo);
        if(userData){
            this.icon_home ='mdi-arrow-left'
        }
        },

  }
</script>

